body {
  padding: 2em;
  font-family: Arial, Helvetica, sans-serif;    
}

#prompt-section {
  margin: 0 auto;
  width: 70%;
}

#prompt-input {
  width: 100%;
  height: 15em;
  
}

#prompt-submit-btn {
  float: right;
  border: 1px solid rgb(0, 110, 255);
  padding-right: 2em;
  padding-left: 2em;
  padding-top: 1em;
  padding-bottom: 1em;
  background-color: rgb(0, 110, 255);
  color: white;
}

#prompt-submit-btn:hover {
  border: 1px solid rgb(0, 68, 255);
  background-color: rgb(0, 68, 255);
  cursor: pointer;
}

#prompt-submit-btn:active {
  border: 1px solid rgb(0, 147, 184);
  background-color: rgb(0, 147, 184);
}

#response-section {
  margin: 0 auto;
  width: 70%;
}

#clear-btn {
  border: 1px solid black;
  padding: 1em;
  background: none;
  cursor: pointer;
  transition: color 0.5s;
  font-size: 12px;
}

#clear-btn:hover {
  border: 1px solid red;
  color: red;
}

.completions {
  background-color: rgb(230, 230, 230);
  padding: 1em;
  margin-top: 1em;
  border-spacing: 1em;
  width: 100%;
}

td {
  vertical-align:top
  
}